@import "../../assets/css/index.scss";
@import "../../assets/css/animation.scss";
@font-face {
    font-family: cb;
    src: local('cb'), url("../../assets/font/custom-black.otf") format('opentype');
}

.home {
    width: 100%;
    height: 100%;
    .header {
        @extend .flex-row-reverse;
        position: fixed;
        width: 100%;
        height: 4rem;
        z-index: 600;
        .top-bar {
            width: 2.5rem;
            height: 2.5rem;
            margin: 1.2rem;
            background: url("../../assets/image/2019.svg") no-repeat;
            background-size: 100% 100%;
        }
    }
    .footer {
        @extend .flex-row-centerP-centerS;
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 4rem;
        z-index: 600;
        @at-root .menu {
            width: 100%;
            height: 100%;
            @extend .flex-row-spacearoundP-centerS;
            li {
                @extend .flex-column-spacearoundP-centerS;
                height: 90%;
                cursor: pointer;
                .icon {
                    width: 1.6rem;
                    height: 1.6rem;
                    &.about {
                        background: url("../../assets/image/qq.svg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.skills {
                        background: url("../../assets/image/wechat.svg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.work {
                        background: url("../../assets/image/github.svg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.music {
                        background: url("../../assets/image/csdn.svg") no-repeat;
                        background-size: 100% 100%;
                    }
                    &.contact {
                        background: url("../../assets/image/youku.svg") no-repeat;
                        background-size: 100% 100%;
                    }
                }
                .label {
                    @extend .flex-row-centerP-centerS;
                    @include font-custom("cb",
                    0.6rem,
                    $c-white,
                    normal,
                    4px);
                }
            }
        }
    }
}